.map {
  position: relative;
  z-index: 10;
  color: var(--white);
  background: var(--black);
  padding: 100px 0px;

  &__inner {
    padding: 70px;
    padding-top: 58px;
    background: linear-gradient(123.3deg, #A96BF7 2.02%, #3103B5 101.68%);
    border-radius: 24px;
    position: relative;
    

    &::before {
      content: url("../img/circle1.png");
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1;
      bottom: -433px;
      left: -82px;
    }
    &::after {
      content: url("../img/circle1.png");
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1;
      top: -77px;
      right: -1049px;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }

  &__title {
    font-weight: bold;
    font-size: 40px;
    line-height: 72px;
    margin-bottom: 4px;
  }

  &__subtitle {
    font-size: 20px;
    line-height: 36px;
  }

  &__checks {
    margin-bottom: 44px;
    display: flex;
    flex-direction: column;
  }

  &__check {
    display: flex;
    
  }

  &__check-svg {
    margin-right: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__check-text {
    font-weight: bold;
    font-size: 20px;
    line-height: 36px;
  }

  &__btns {
    display: flex;
  }

  &__btn {
    padding: 16px 40px;
    margin-right: 28px;
    font-weight: bold;
    font-size: 20px;
    line-height: 36px;
    color: var(--black);
    background: linear-gradient(180deg, #FFD600 0%, #FFB800 100%);
    box-shadow: 0px 9px 44px rgba(255, 214, 0, 0.19);
    border-radius: 16px;
    &:hover {
      background: linear-gradient(180deg, #FFDE2E 0%, #FFC42E 100%);
    }
    &:active {
      background: linear-gradient(180deg, #E3BF00 0%, #E4A400 100%);
    }
  }

  &__btn2 {
    padding: 16px 40px;
    font-weight: bold;
    font-size: 20px;
    line-height: 36px;
    color: var(--white);
    background: rgba(255, 255, 255, 0.15);
    border-radius: 16px;
    transition: all .3s ease;

    &:hover {
      background: rgba(255, 255, 255, 0.25);
      
    }
    &:active {
      background: rgba(255, 255, 255, 0.08);
    }
  }
}


@include px1024 {
  .map {
    padding: 20px 0px;

    &__inner {
      padding: 52px;
      &::before {
        content: "";
      }

      &::after {
        content: "";        
      }
    }

    &__text {
      margin-bottom: 28px;
    }

    &__title {
      font-size: 36px;
      line-height: 60px;
      margin-bottom: 8px;
    }

    &__subtitle {
      font-size: 18px;
      line-height: 28px;
    }

    &__checks {
      margin-bottom: 40px;
    }

    &__check {
      
    }

    &__check-svg {
      margin-right: 12px;
    }

    &__check-text {
      font-size: 18px;
      line-height: 32px;
    }

    &__btns {}

    &__btn {
      padding: 12px 36px;
      font-size: 18px;
      line-height: 36px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 16px;
    }

    &__btn2 {
      padding: 12px 34px;
      font-size: 18px;
      line-height: 36px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}


@include px768 {
  .map {
    padding: 0px;

    &__inner {
      padding: 16px;
      border-radius: 16px;

      &::before {
        content: "";
      }

      &::after {
        content: "";
      }
    }

    &__text {
      margin-bottom: 20px;
    }

    &__title {
      font-size: 22px;
      line-height: 28px;
      margin-bottom: 8px;
    }

    &__subtitle {
      font-size: 14px;
      line-height: 22px;
    }

    &__checks {
      margin-bottom: 28px;
    }

    &__check {
      margin-bottom: 8px;
    }

    &__check-svg {
      margin-right: 8px;
      transform: scale(.7);
    }

    &__check-text {
      font-size: 14px;
      line-height: 20px;
    }

    &__btns {
      flex-wrap: wrap;
    }

    &__btn {
      flex: 1 1 100%;
      font-size: 16px;
      line-height: 32px;
      border-radius: 12px;
      margin-right: 0px;
      margin-bottom: 12px;
    }

    &__btn2 {
      flex: 1 1 100%;
      font-size: 16px;
      line-height: 32px;
      border-radius: 12px;
    }
  }
}