.more {
  position: relative;
  z-index: 10;
  color: var(--white);
  background: var(--black);

  &__inner {
    padding: 80px 0px;
  }

  &__title {
    font-weight: bold;
    font-size: 48px;
    line-height: 92px;
    text-align: center;
    margin-bottom: 52px;
    display: block;
    position: relative;
    &::before {
      content: url("../img/paints4.png");
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1;
      top: 15px;
      left: 0px;
    }
  }

  &__cards {
    display: grid;
    gap: 44px;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }

  &__card {
    padding: 28px;
    background: #1B2A40;
    border-radius: 12px;
    transition: all .2s ease;

    &:hover {
      background: #223451;
    }
    &:active {
      background: #223451;
    }
  }

  &__card-img-block {
    margin: -28px;
    margin-bottom: 20px;
    border-radius: 12px 12px 0 0;
  }

  &__card-img {
    border-radius: 12px 12px 0 0;
    width: 100%;
  }

  &__card-text {}

  &__card-text-title {
    font-weight: bold;
    font-size: 15px;
    line-height: 36px;
    color: var(--yellow2);
    margin-bottom: 2px;
  }

  &__card-text-subtitle {
    font-weight: bold;
    font-size: 20px;
    line-height: 32px;
    color: var(--white);
  }
}




@include px1024 {
  .more {

    &__inner {
      
    }

    &__title {
      font-size: 40px;
      line-height: 52px;
      margin-bottom: 44px;
      &::before {
        content: url("../img/paints4.png");
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: -1;
        top: 15px;
        left: 0px;
        transform: scale(.9);
      }
    }

    &__cards {
      gap: 28px;
    }

    &__card {
      padding: 16px 20px;
    }

    &__card-img-block {
      margin: -16px -20px;
      margin-bottom: 16px;
    }

    &__card-img {}

    &__card-text {}

    &__card-text-title {
      font-size: 15px;
      line-height: 32px;
      margin-bottom: 4px;
    }

    &__card-text-subtitle {
      font-size: 16px;
      line-height: 24px;
    }
  }
}

@include px768 {
  .more {

    &__inner {}

    &__title {
      font-size: 28px;
      line-height: 28px;
      margin-bottom: 28px;

      &::before {
        content: url("../img/paints4.png");
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: -1;
        top: 10px;
        left: 0px;
        transform: scale(.6);
      }
    }

    &__cards {
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr 1fr;
      gap: 36px;
    }

    &__card {
      padding: 16px 20px;
    }

    &__card-img-block {
      margin: -16px -20px;
      margin-bottom: 16px;      
    }

    &__card-img {
      object-fit: cover;
      object-position: 50% 50%;
      max-height: 120px;
    }

    &__card-text {
      margin-bottom: 8px;
    }

    &__card-text-title {
      font-size: 15px;
      line-height: 32px;
      margin-bottom: 4px;
    }

    &__card-text-subtitle {
      font-size: 16px;
      line-height: 24px;
    }
  }
}