.header {
  background: transparent;
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;


  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
  }

  &__logo-wrap {
    display: flex;
    align-items: center;
    font-size: 28px;
    color: #ffffff;
    font-weight: bold;
    text-transform: capitalize;
    letter-spacing: 0.5px;
  }

  &__logo-wrap img{
    margin-right: 13px;
  }

  &__logo {
    
  }

  &__burger {
    cursor: pointer;
    display: none;
    border-radius: 50%;
    
  }

  &__nav {}



  &__ul {
    display: grid;
    grid-auto-flow: column;
    gap: 0px;
  }

  &__ul-logo {
    display: none;
  }

  &__li {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 52px;
  }
  &__li:first-of-type {
    margin-left: 0px;
  }

  &__a {
    color: #FFFFFF;
    opacity: 0.9;
    white-space: nowrap;
    padding: 15px 0px;
    font-size: 18px;
    transition: all .3s ease;
    font-weight: bold;

    &:hover {
      color: #FFD201;
    }
  }

  &__btn-group {
    margin-left: 72px;
    display: flex;
    align-items: center;
  }

  &__btn {
    display: flex;
    justify-content: space-between;
    background: #262E37;
    color: var(--white);
    padding: 10px 16px;
    border-radius: 8px;
    transition: all .3s ease;
    font-weight: bold;
    display: none;
    &:hover {
      background: #00ACEE;
      color: var(--white);
    }
    &:active {
      background: #0094CD;
    }
  }  

  &__btn-group a:first-child {
    display: flex;
  }

  &__btn > svg {
    margin-right: 10px;
  }

}

.header__btn-svg>path {
  transition: all .3s ease;
}

.header__btn:hover .header__btn-svg > path {
  transition: all .3s ease;
  fill: #ffffff;
}

.container {}

.header-background {
  background: var(--black) !important;
}


.line {
  fill: none;
  stroke: var(--white);
  stroke-width: 6;
  transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
              stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}

.line1 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}

.line2 {
  stroke-dasharray: 60 60;
  stroke-width: 6;
}

.line3 {
  stroke-dasharray: 60 207;
  stroke-width: 6;
}

.opened .line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
  stroke: var(--white);
  transition: all 0.6s ease;
}

.opened .line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30;
  stroke-width: 6;
  stroke: var(--white);
  transition: all 0.6s ease;
}

.opened .line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 6;
  stroke: var(--white);
  transition: all 0.6s ease;
}



@include px1024 {
  .header {
    &__li {
      margin-left: 40px;
    }
    &__a {
      font-size: 16px;
      line-height: 28px;
    }
    &__btn-group {
      margin-left: 50px;
    }
    
    &__btn-name {
      
      
    }
  }
}


@include px768 {
  .header {

    &__inner {
      height: 60px;
    }

    &__logo-wrap > svg {
      width: 140px;
    }

    &__logo {}

    &__burger {
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      z-index: 101;
    }

    &__nav {
      background: #222930;
      transition: all .5s ease;
      position: absolute;
      top: 0%;
      right: -100%;
      width: 100%;
      height: 100vh;
      transition: all .5s ease;
      overflow: auto;
    }

    &__ul {      
      margin-top: 50px;
      display: grid;
      grid-auto-flow: row;
      padding: 32px 20px;
      gap: 16px;
    }

    &__ul-logo {
      display: flex;
      justify-content: center;
      margin-bottom: 36px;
    }

    &__li {
      width: 100%;
      height: 48px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: #383E44;
      margin-left: 0px;
      border-radius: 12px;
      transition: all .3s ease;
      &:hover {
        background: rgba(255, 255, 255, 0.2);
      }
      &:active {
        background: rgba(255, 255, 255, 0.1);
      }
    }

    &__a {
      width: 100%;
      font-weight: bold;
      color: var(--white);
      font-size: 15px;
      padding: 15px 20px;
      min-width: 248px;
      transition: all .3s ease;
      &:hover {
      }
    }

    &__btn-group {
      
    }

    &__btn {
      width: 100%;
      height: 100%;
      border-radius: 12px;
      font-size: 15px;
      display: flex;
      flex-direction: row-reverse;
      margin-bottom: 16px;
      background: #383E44;
      font-weight: bold;
      color: var(--white);
      font-size: 15px;
      padding: 15px 20px;
      min-width: 288px;
      align-items: center;
    }

    &__btn>svg {
     margin-right: 0px;
    }

    &__btn > svg > path {
      fill: var(--white-grey);
    }

    &__btn-group {
      margin-top: 32px;
      margin-left: 0;
      display: flex;
      flex-direction: column;
      width: 100%;
    }

    
    
  }

  .container {}

  .header__nav-active {
    position: absolute;
    top: 0%;
    right: 0%;
    transition: all .5s ease;
  }
}

