.creators {
  color:  var(--white);
  position: relative;
  z-index: 10;
  background: var(--black);

  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 100px;
  }

  &__title-group {
    display: flex;
    justify-content: center;
    margin-bottom: 80px;
  }

  &__title {
    position: relative;
    font-weight: bold;
    font-size: 48px;

    &::before {
      content: url("../img/paints2.png");
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1;
      top: 18px;
      left: -50px;
    }
  }

  &__title2 {
    display: none;
  }

  &__title-span {
    position: relative;

    
  }

  &__points {
    display: flex;
    justify-content: space-between;
    
    max-width: 936px;
    width: 100%;
  }

  &__point {
    max-width: 252px;
    width: 100%;
  }

  &__point-points {
    display: flex;
    margin-bottom: 28px;
  }

  &__point-point {
    margin-right: 12px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background: var(--blue);
  }

  &__point-title {
    font-weight: bold;
    font-size: 24px;
    line-height: 1.3;
    margin-bottom: 10px;
    color: var(--yellow2);
  }

  &__point-subtitle {
    font-weight: normal;
    font-size: 20px;
    line-height: 1.5;
    color: var(--white);
  }
}



@include px1024 {
  .creators {
    

    &__inner {
      padding-top: 80px;
    }

    &__title-group {
      margin-bottom: 60px;
    }

    &__title {
      font-size: 40px;
      line-height: 52px;

      &::before {
        content: url("../img/paints2.png");
        transform: scale(0.9);
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: -1;
        top: 18px;
        left: -105px;
      }
    }

    &__title2 {}

    &__title-span {}

    &__points {}

    &__point {
      max-width: 220px;
    }

    &__point-points {
      margin-bottom: 20px;
    }

    &__point-point {
      width: 20px;
      height: 20px;
    }

    &__point-title {
      font-size: 20px;
    }

    &__point-subtitle {
      font-size: 16px;
    }
  }
}



@include px768 {
  .creators {
    

    &__inner {
      padding-top: 40px;
    }

    &__title-group {
      margin-bottom: 60px;
      max-width: 400px;
    }

    &__title {
      display: none;
    }

    &__title2 {
      display: inline-block;
      font-size: 28px;
      text-align: center;
    }

    &__title-span {
      display: block;
      &::before {
        content: url("../img/paints3.png");
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: -1;
        top: 10px;
        left: -4px;
      }
    }

    &__points {
      flex-wrap: wrap;
      flex-direction: column;
      align-items: center;
    }

    &__point {
      max-width: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      max-width: 400px;
      margin-bottom: 20px;
    }

    &__point-points {
      margin-bottom: 20px;
      display: none;
    }

    &__point-point {
      width: 20px;
      height: 20px;
    }

    &__point-title {
      font-size: 18px;
      margin-bottom: 4px;
    }

    &__point-subtitle {
      font-size: 14px;
    }
  }
}


