.app {
  color: var(--white);
  background: var(--black);
  position: relative;
  z-index: 10;
  padding: 100px 0;

  &::before {
    content: url("../img/bg-1.png");
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -2;
    top: -55px;
    right: -90%;
  }


  &__inner {
    border: 3px solid var(--yellow2);
    border-radius: 24px;
    padding: 80px;
    padding-top: 70px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: rgba(255, 255, 255, 0.02);
    position: relative;
    z-index: 1;
    max-width: 1064px;
    margin: 0 auto;
    &::before {
      content: url("../img/bg-2.png");
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -2;
      top: 80%;
      left: -20%;
    }
  }

  &__apps {
    margin-bottom: 60px;
  }

  &__apps-title-group {
    display: flex;
    justify-content: center;   
    align-items: center;
    margin-bottom: 28px;
  }

  &__apps-title-svg {
    margin-right: 8px;
  }

  &__apps-title {
    color: var(--yellow2);
    font-weight: bold;
    font-size: 28px;
    line-height: 36px;
  }

  &__apps-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &__apps-card {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px 20px;
    padding-left: 8px;
    margin-right: 24px;
    margin-bottom: 20px;
    background: #2C3034;
    border-radius: 12px;
  }

  &__apps-card:last-child {
    margin-right: 0;
  }

  &__apps-card-left {
    margin-right: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    border-radius: 8px;
    background: var(--yellow2);
  }

  &__apps-card-circle {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: green;
  }

  &__apps-card-right {
    font-weight: bold;
    font-size: 18px;
  }

  &__system {
    margin-bottom: 60px;
  }

  &__system-title-group {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 28px;
  }

  &__system-title-svg {
    margin-right: 8px;
  }

  &__system-title {
    color: var(--yellow2);
    font-weight: bold;
    font-size: 28px;
    line-height: 36px;
  }

  &__system-cards {
    display: flex;
    justify-content: space-between;   
    max-width: 904px; 
  }

  &__system-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 32px;
    background: #2C3034;
    max-width: 430px;
    margin-right: 42px;
    border-radius: 16px;
  }

  &__system-card:last-child {
    margin-right: 0px;
  }

  &__system-card-svg {
    margin-bottom: 16px;
  }

  &__system-card-svg-special {
    margin-right: 8px;
  }

  &__system-card-text {
    text-align: center;
  }

  &__system-card-title {
    font-weight: bold;
    color:  var(--yellow2);
    font-size: 24px;
    line-height: 1.6;
  }

  &__system-card-subtitle {
    color: var(--white);
    font-size: 20px;
    line-height: 1.4;
  }

  &__blockchains {}

  &__blockchains-title-group {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 28px;
  }

  &__blockchains-title-svg {
    margin-right: 8px;
  }

  &__blockchains-title {
    color: var(--yellow2);
    font-weight: bold;
    font-size: 28px;
    line-height: 36px;
  }

  &__blockchains-cards {
    display: grid;
    gap: 24px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }

  &__blockchains-card {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background: #2C3034;
    padding: 14px 24px;
    border-radius: 16px;
  }

  &__blockchains-card-left {
    margin-right: 16px;
  }

  &__blockchains-card-right {
    font-weight: bold;
    font-size: 18px;
  }
}



@include px1024 {
  .app {
    padding: 80px 0px;
    &::before {
      content: "";
    }

    &__inner {
      padding: 36px;
      
      margin: 0 auto;
      &::before {
        content: "";
      }
    }
    &__apps {
      margin-bottom: 40px;
    }
    &__system {
      margin-bottom: 40px;
    }

    &__apps-title-group {
      margin-bottom: 24px;
    }
    &__system-title-group {
      margin-bottom: 24px;
    }
    &__blockchains-title-group {
      margin-bottom: 24px;
    }

    &__apps-title {
      font-size: 24px;
    }

    &__apps-title-svg {
      transform: scale(.8);
    }

    &__system-title {
      font-size: 24px;
    }
    &__blockchains-title{
      font-size: 24px;
    }

    &__apps-card {
      margin-right: 16px;
      margin-bottom: 16px;
    }

    &__apps-card-left {
      width: 36px;
      height: 36px;
      border-radius: 8px;
      margin-right: 12px;
      
    }

    &__apps-card-circle {
      width: 18px;
      height: 18px;
    }

    &__apps-card-right {
      font-size: 16px;
      line-height: 32px;
    }

    &__system-card {
      margin-right: 24px;
    }

    &__system-card-title {
      font-size: 22px;
      line-height: 36px;
    }
    &__system-card-subtitle {
      font-size: 16px;
      line-height: 28px;
    }

    &__system-card {
      flex: 0 1 50%;
      padding: 24px;
    }

    &__system-card-svg-special {
      margin-right: 8px;
    }

    &__blockchains-cards {
      gap: 16px;
    }
    &__blockchains-card {
      border-radius: 12px;
      padding: 6px 16px;
    }
    &__blockchains-card-left {
      margin-right: 8px;
      display: flex;
      align-items: center;
    }
    &__blockchains-card-left > svg {
      transform: scale(.75);
    }
    &__blockchains-card-right {
      font-size: 14px;
    }
  }
  .svg-special {
    transform: scale(.8);
  }
}



@include px768 {
  .app {
    &__inner {
      padding: 16px;
    }

    &__apps {
      margin-bottom: 40px;
    }

    &__system {
      margin-bottom: 40px;
    }

    &__system-cards {
      flex-wrap: wrap;
      justify-content: center;
    }

    &__apps-title-group {
      margin-bottom: 16px;
    }

    &__system-title-group {
      margin-bottom: 16px;
    }

    &__blockchains-title-group {
      margin-bottom: 16px;
    }

    &__apps-title {
      font-size: 18px;
    }

    &__system-title {
      font-size: 18px;
      display: flex;
      align-items: center;
      line-height: 24px;
    }

    &__blockchains-title {
      font-size: 18px;
    }

    &__apps-card {
      margin-right: 8px;
      margin-bottom: 8px;
      padding: 0px 8px;
      border-radius: 4px;
    }

    &__apps-card-left {
      width: 8px;
      height: 8px;
      margin-right: 8px;
    }
    &__apps-card-circle {
      width: 8px;
      height: 8px;
    }

    &__system-card-svg {
      margin-bottom: 12px;
      display: none;
    }

    &__apps-card-right {
      font-size: 14px;
      line-height: 24px;
    }

    &__system-card {
      margin-right: 0px;
      padding: 16px 12px;
    }

    &__system-card-title {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 4px;
    }

    &__system-card-subtitle {
      font-size: 14px;
      line-height: 24px;
    }

    &__system-card {
      flex: 1 1 100%;
      margin-bottom: 16px;
    }

    &__blockchains-cards {
      gap: 12px;
      grid-template-columns: 1fr 1fr;
      grid-auto-rows: 1fr 1fr;
    }

    &__blockchains-card {
      border-radius: 12px;
      padding: 0px 6px;
      
    }

    &__blockchains-card-left {
      margin-right: 2px;  
      display: flex;
    }

    &__blockchains-card-left>svg {
      transform: scale(.65);
    }

    &__blockchains-card-right {
      font-size: 12px;
    }
  }
  .svg-special {
    transform: scale(.75);
  }
}