@import "vars";
@import "mixins";


@import "./components/header";
@import "./components/home";
@import "./components/creators";
@import "./components/app";
@import "./components/map";
@import "./components/more";
@import "./components/backed";
@import "./components/footer";


//grid
.container {
  margin: 0 auto;
  padding: 0 16px;
  max-width: 1172px;
}

.paddings {
  padding: 100px 0px;
  position: relative;
  z-index: 10;
}

.bg-circle {
  background: #1D3245 !important;
}

.yellow {
  background: #FFE501;
}
.bg-yellow {
  background: rgba(255, 229, 1, 0.15);
}

.blue {
  background: #21A6FA;
}

.bg-blue {
  background: rgba(33, 166, 250, 0.15);
}

.purple {
  background: #D04BFF;
}

.bg-purple {
  background: rgba(208, 75, 255, 0.15);
}

.green {
  background: #01FFB3;
}

.bg-green {
  background: rgba(1, 255, 179, 0.15);
}

.pink {
  background: #FF49B6;
}

.bg-pink {
  background: rgba(255, 73, 182, 0.15);
}

.salat {
  background: #86F045;
}

.bg-salat {
  background: rgba(134, 240, 69, 0.15);
}

.orange {
  background: #FF792E;
}

.bg-orange {
  background: rgba(255, 121, 46, 0.15);
}


@include px1024 {
  .paddings {
    padding: 80px 0px;
  }
}

@include px800 {
  .paddings {
    padding: 30px 0px;
  }
}